code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  box-sizing: border-box;
  text-decoration: none;
}

nav {
  background: rgb(40 45 59);
  color: white;
  display: grid;
  padding: 10px;
  color: white;
  grid-auto-flow: column;
  justify-content: space-between;
  font-family: 'Alata';
  background: #141E30;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

nav a {
  color: white;
}


.progress{
  height: 1.5rem;
border-radius: .25rem;
background: linear-gradient(to right, rgb(40 45 59) 25%, rgb(53 76 108) 25%, rgb(136, 93, 218) 0%, rgba(110, 62, 201, 0.72) 100%);
margin: 5px 0px;
color: white;
font-weight: bold;
text-align: center;
padding: 0 10px;
font-size: small;
display: flex;
align-items: center;
}


.book {
  display: grid;
}

.img {
  height: 30vh;
  border: 1px solid black;
  border-radius: 3px;
  /* aspect-ratio: 1/2; */
  width: 30vw;
}

.bookDiv {
  display: flex;
  grid-auto-flow: column;
  gap: 1rem;
  padding: 5px;
  position: relative;
  padding-top: 3.5rem;
}

.collection-index-title{
  background: #1e1929;
  color: white;
  padding: 5px;
  font-family: alata;
  color: burlywood;
}

