.container {
  display: grid;
  border: 2px #2d4a52 dashed;
  margin: 3px;
  margin-top: 1rem;
  overflow-x: scroll;
  border-radius: 3px;
}


.field-container {
  display: grid;
  background: rgb(33 38 45);
  padding: 10px;
  gap: 10px;
}

textarea.classic ,input.classic,select.classic {
  background: #1e1929;
  border-radius: 3px;
  border-width: 0!important;
  color: #fff;
  font-family: Roboto Mono;
  font-family: Roboto;
  font-size: small;
  min-width: 50px;
  padding: 5px;
  height: 2rem;
  font-family: monospace;
  background: #3b3b3b;
}

.fieldDiv {
  display: grid;
  color: white;
  font-family: alata;
}


.addToCollBtn {
  background: #9014d3;
  border: #ffdead;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-family: monospace;
  padding: 7px;
  right: 20px;
}

.btn-pos{
  background: #9014d3;
  border: #ffdead;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-family: monospace;
  padding: 7px;
  right: 20px;
  text-align: center;
}

a.btn-pos{
  background: #9014d3;
  border: #ffdead;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-family: monospace;
  padding: 7px;
  right: 20px;
  color: white;
  text-decoration: none;
  display: flex;
  gap: 10px;
  align-items: center;
}


.progressDiv{
  height: 1.5rem;
border-radius: .25rem;
background: linear-gradient(to right, rgb(40 45 59) 25%, rgb(53 76 108) 25%, rgb(136, 93, 218) 0%, rgba(110, 62, 201, 0.72) 100%);
margin: 5px 0px;
color: white;
font-weight: bold;
text-align: center;
padding: 0 10px;
font-size: small;
display: flex;
align-items: center;
}

.avlble-fields-div {
  display: grid;
  max-width: 400px;
  width: -moz-fit-content;
  width: fit-content;
  color: white;
  padding: 5px;
  border: 2px solid #452727;
  border-radius: 3px;
  background: #171c27;
}

.avlble-fields-div >* .field {
  display: flex;
  justify-content: space-between;
  font-family: "Roboto mono";
  font-size: small;
  background: #21262d;
  padding: 3px;
  border-radius: 3px;
}

.option-hover:hover{
  background-color: #1e1929;
}
.option-hover{
  padding: 5px;
  border-radius: 3px;
  display: flex;
  align-items: center;
}

.container-righticon{
  padding: 3px;
  border: 2px solid rgb(69, 39, 39);
  border-radius: 5px;
  background: rgb(23 28 39 / 76%);

}


.container-righticon > .titlediv {
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  font-family: Alata;
}


.url-Btn-blue{
  background: #9014d3;
  border: #ffdead;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-family: monospace;
  padding: 7px;
  width: 100%;
  display: block;
  text-align: center;
  font-family: 'Roboto Mono';
  text-decoration: none;
  right: 20px; 
}


.new_coll_router_l{
    padding: 5px;
    border-radius: 5px;
}

.new_coll_router_l.active{
    background: orange;
}

.new_coll_container{
  padding: 8px;
  display: grid;
  gap: 8px;
}


/* div:where(.swal2-container) button:where(.swal2-styled){
  padding: 0px!important;
} */


.suggested-coll-div {
  padding: 4px;
  color: white;
  font-family: 'Alata';
  gap: 5px;
  display: flex;
  align-items: center;
  background: #432255;
  justify-content: space-between;
}

#search-div{
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  gap: 5px;
  background: #412f53;
  padding: 3px;
  border-radius: 5px;
}


.tip-option {
  padding: 8px;
  cursor: pointer;
}

.tip-option.active {
  background: cornflowerblue;
}